.Keyboard {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: default;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Keyboard__Row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.Keyboard__Key {
  -ms-flex: 1 1;
      flex: 1 1;
}

.Keyboard__Key--tab {
  -ms-flex: 1.5 1;
      flex: 1.5 1;
}

.Keyboard__Key--caps,
.Keyboard__Key--enter {
  -ms-flex: 2 1;
      flex: 2 1;
}

.Keyboard__Key--shift {
  -ms-flex: 2.8 1;
      flex: 2.8 1;
}

.Keyboard__Key--backspace {
  -ms-flex: 2 1;
      flex: 2 1;
}

.Keyboard__Key--space {
  -ms-flex: 5 1;
      flex: 5 1;
}

.Keyboard__Key {
  color: #444;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 5%;
  height: 5%;
  line-height: 40px;
  text-align: center;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 21px;
  font-weight: bolder;
  margin: 0.3%;
  background: -webkit-gradient(linear, left top, left bottom, from(#f6f6f6), to(#ededed));
  background: -webkit-linear-gradient(#f6f6f6, #ededed);
  background: -o-linear-gradient(#f6f6f6, #ededed);
  background: linear-gradient(#f6f6f6, #ededed);
}

.Keyboard__Key--highlighted {
  background: #2879c9 !important;
  color: #fff;
  border-color: black;
}

Keyboard__Key--finger {
  background: -webkit-gradient(linear, left top, left bottom, from(#f6f6f6), to(#e4e3e3));
  background: -webkit-linear-gradient(#f6f6f6, #e4e3e3);
  background: -o-linear-gradient(#f6f6f6, #e4e3e3);
  background: linear-gradient(#f6f6f6, #e4e3e3);
}

.Keyboard__Key--space,
.Keyboard__Key--empty {
  color: transparent; /* FIXME: there should be no text to hide */
}



