.TextToType {
  font-size: 2.5em;
  font-family: "Rubik", "Roboto Condensed", sans-serif;
  -webkit-font-variant-ligatures: none;
          font-variant-ligatures: none;
  font-weight: bolder;
  letter-spacing: 1px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: default;
}

.TextToType__snippet--correct {
  color: #ddd;
}
.TextToType__snippet--error {
  color: #d41341;
}
.TextToType__snippet--error space {
  text-decoration: underline;
}
.TextToType__snippet--current {
  color: #2879c9;
  text-decoration: underline;
}
.TextToType__snippet--untyped {
  color: #444;
}



